import _ from 'lodash';
import { Box } from '@mui/system';
import { useCallback } from 'react';
import BasicTable from "../../reusableComponents/TableComponent";


function DisplayRemidiation({ tableData }) {
    
    function getLocation(loc) {
        let lat = _.get(loc, 'currentlocation.latitude', '-');
        let lon = _.get(loc, 'currentlocation.longitude', '-');
        return `${lat},${lon}`;
    }

    const getRowData = useCallback((data) => {
        return {
            deviceID: _.get(data, 'ucid', 'device id not found'),
            currentLoc: getLocation(data),
            hardware: _.get(data, 'hardware.description', 'no description'),
            manufacturer: _.get(data, 'manufacturer.name', 'no manufacturer found'),
            owner: _.get(data, 'owner.name', '-'),
            software: _.get(data, 'software.description', '-')
        }
    }, []);

    let row = tableData.map((rowData) => {
        return getRowData(rowData.data)
    })

    return (
        <>
            {row.length > 0 ?
                <Box display='flex' justifyContent='center' maxWidth='80vw' margin='auto'>
                    <BasicTable rows={row} />
                </Box>
                :
                <div>No data to display</div>
            }
        </>
    )
};

export default DisplayRemidiation;