import { useEffect, useState, useCallback } from 'react';
import logo from './logo.svg';
import logoDark from './logo/ucid-logo-dark.png';
import logoWhite from './logo/ucid-logo-white.png';

import _ from 'lodash';

import Stack from '@mui/material/Stack';
import Button from '@mui/material/Button';

import Navbar from './components/Navbar/Navbar';
import SetRemediation from './components/SetRemediation/SetRemediation';

import './App.css';

import { Box } from '@mui/system';
import BasicTable from './reusableComponents/TableComponent';
import DisplayRemidiation from './components/DisplayRemediation/DisplayRemediation';
import RemediationButtons from './components/RemediationButtons/RemediationButtons';
import axios from 'axios';
import { Alert, CircularProgress, Skeleton } from '@mui/material';


function App() {
  const [deviceID, setDeviceId] = useState('');
  const [tableData, setTableData] = useState([]);
  const [showCreateRemediation, setShowCreateRemedition] = useState(false);
  const [showDisplayRemediation, setShowDisplayRemediation] = useState(false);
  const [showLoader, setShowLoader] = useState(false);
  const [remediationStatus, setRemediationStatus] = useState('');
  const [remediationAlert, setRemediationAlert] = useState('');

  const [listOfDeviceID, setListOfDeviceID] = useState([]);

  const username = 'regu1'
  const password = 'passuser'

  const baseURL = 'http://65.2.132.238:4000/api/regulator/';

  const handleClickSetRemediation = useCallback(() => {
    setShowCreateRemedition(true);
    setShowDisplayRemediation(false);
    setShowLoader(false)
  }, [showCreateRemediation, showDisplayRemediation, showLoader]);

  const handleClickDisplayRemediation = useCallback(async () => {

    setShowCreateRemedition(false);
    setShowDisplayRemediation(true);
    // API call GET Device information.
    // const deviceID = '80f291e9-4a08-44d8-bf2a-205249ae1faf';

    setShowLoader(true);
    const getRequestOptions = {
      method: 'GET',
      headers: {
        'Authorization': 'Basic ' + btoa(`${username}:${password}`),
      },
      mode: 'cors',
      cache: 'default'
    };

    const neededDeveicesRes = await fetch(`${baseURL}/event/RemediationNeeded`, getRequestOptions);
    const neededDevicesData = await neededDeveicesRes.json();
    let devicesToBeDisplayed = [];
    let devicesList = [];
    if (neededDevicesData.success) {
      devicesToBeDisplayed = neededDevicesData.data.slice(0, 10);
    }
    if (!_.isEmpty(devicesToBeDisplayed)) {
      devicesList = devicesToBeDisplayed.map((ele) => {
        return ele.eventData.deviceID
      });
      setListOfDeviceID(devicesList)
    }

    const remediationTableData = await Promise.all(
      devicesList.map(async (device) => {
        const tableRowRes = await fetch(`${baseURL}/device/search?ucid=${device}`, getRequestOptions);
        const tableRowData = await tableRowRes.json();
        return tableRowData;
      }));

    setTableData(remediationTableData);
    setShowLoader(false);
  }, [showCreateRemediation, showDisplayRemediation, tableData, showLoader, deviceID, listOfDeviceID]);

  const handleChangeDeviceId = useCallback((event) => {
    setDeviceId(event.target.value);
  }, [deviceID]);

  const handleSetRemediation = useCallback(async () => {
    // make flag remedition api call with identifier as deviceId
    const requestOptions = {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json',
        'Authorization': 'Basic ' + btoa('regu1:passuser'),
      },
      body: JSON.stringify({ identifier: `${deviceID}` })
    };
    setShowLoader(true);
    const setRemediationResponse = await fetch(`${baseURL}device/flag`, requestOptions);
    const setRemediationData = await setRemediationResponse.json();

    if (setRemediationData.success) {
      setRemediationStatus(setRemediationData.data);
      setRemediationAlert('');
    } else {
      setRemediationStatus('');
      setRemediationAlert(setRemediationData.message);
    }
    setShowLoader(false);
  }, [deviceID, remediationStatus, showLoader]);

  return (
    <div className="App">
      <Navbar logo={logoWhite} />

      <RemediationButtons
        handleClickSetRemediation={handleClickSetRemediation}
        handleClickDisplayRemediation={handleClickDisplayRemediation}
      />
      {
        showLoader &&
        <Box sx={{ display: 'flex', justifyContent: 'center', alignItems: 'center' }}>
          <CircularProgress />
        </Box>
      }
      {showCreateRemediation && !showLoader &&
        <>
          <SetRemediation
            deviceID={deviceID}
            handleChangeDeviceId={handleChangeDeviceId}
            handleSetRemediation={handleSetRemediation}
          />
          {(remediationStatus.length > 0 || remediationAlert.length) > 0 &&
            <Stack
              sx={{ width: '100%', mt: 1, }}
              maxWidth={{
                xs: '80%', sm: '75%', md: '60%', lg: '50%'
              }}
              margin='auto'
              spacing={2}>
              {remediationStatus && <Alert severity="success">{remediationStatus}</Alert>}
              {remediationAlert && <Alert severity="error">{remediationAlert}</Alert>}
            </Stack>
          }
        </>
      }
      {showDisplayRemediation && !showLoader &&
        <DisplayRemidiation
          tableData={tableData}
        />}



    </div >
  );
}

export default App;
