import * as React from 'react';
import Table from '@mui/material/Table';
import TableBody from '@mui/material/TableBody';
import TableCell from '@mui/material/TableCell';
import TableContainer from '@mui/material/TableContainer';
import TableHead from '@mui/material/TableHead';
import TableRow from '@mui/material/TableRow';
import Paper from '@mui/material/Paper';


export default function BasicTable({rows}) {
  return (
    <TableContainer component={Paper}>
      <Table sx={{ minWidth: 650 }} aria-label="simple table">
        <TableHead>
          <TableRow>
            {/* <TableCell>Device Id</TableCell> */}
            <TableCell align="right">Devide Id</TableCell>
            <TableCell align="right">Current location &nbsp;(lat, lon)</TableCell>
            <TableCell align="right">Hardware</TableCell>
            <TableCell align="right">Manufacturer</TableCell>
            <TableCell align="right">Owner</TableCell>
            <TableCell align="right">Software</TableCell>
          </TableRow>
        </TableHead>
        <TableBody>
          {rows.map((row) => (
            <TableRow
              key={row.name}
              sx={{ '&:last-child td, &:last-child th': { border: 0 } }}
            >
              {/* <TableCell component="th" scope="row">
                {row.name}
              </TableCell> */}
              <TableCell align="right">{row.deviceID}</TableCell>
              <TableCell align="right">{row.currentLoc}</TableCell>
              <TableCell align="right">{row.hardware}</TableCell>
              <TableCell align="right">{row.manufacturer}</TableCell>
              <TableCell align="right">{row.owner}</TableCell>
              <TableCell align="right">{row.software}</TableCell>
            </TableRow>
          ))}
        </TableBody>
      </Table>
    </TableContainer>
  );
}
