import Stack from '@mui/material/Stack';
import Button from '@mui/material/Button';
import { TextField, Typography } from '@mui/material';

function SetRemediation({ deviceID, handleChangeDeviceId, handleSetRemediation }) {
    return (
        <Stack
            sx={
                {
                    border: 1,
                    //  borderRadius: '50%'
                    display: 'flex',
                    flexDirection: 'column',
                    justifyContent: 'center',
                    alignItems: 'center',
                    p: 4,
                    spacing: 3,
                    m: 'auto',
                    mt: 1,
                    // mb: 1,
                    borderRadius: 2
                }
            }

            maxWidth={
                {
                    xs: '80%', sm: '75%', md: '60%', lg: '50%'
                }
            }
        >
            <Typography variant="h7"
                component="h7"
                mb={2}
            > Enter Device Id to set Remediation:
            </Typography>
            <TextField
                id="outlined-basic"
                label="Device Id"
                variant="outlined"
                value={deviceID}
                onChange={handleChangeDeviceId}
            />
            <Button variant="contained"
                color="info"
                sx={
                    {
                        pl: 5,
                        pr: 5,
                        fontWeight: 'normal',
                        fontSize: 18,
                        mt: 1
                    }
                }
                onClick={handleSetRemediation}
            > Set Remediation
            </Button>
        </Stack>
    )
};

export default SetRemediation;